<template>
  <div class="tabmenu out-tabmenu">
    <ul>
      <!-- 월 날짜 -->
      <li id="tab1" class="btnCon">
        <input type="radio" checked name="tabmenu" id="tabmenu1">
        <label for="tabmenu1">월</label>
        <div class="tabCon">
          <VueDatePicker 
            style="display: block;"
            v-model="date" 
            locale="kr" 
            :enable-time-picker="false" 
            :transitions="false" 
            :week-start="0"
            inline auto-apply 
            :min-date="new Date()" 
            :position="left"
            />

          <!-- 월 시간표 -->
          <div>
            <table>
              <thead>
                <tr>
                  <th>시간</th>
                  <th>내용</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(month, index) in monthitems" :key="index">
                  <td>{{ month.time }}</td>
                  <td>
                    <textarea v-model="month.text"></textarea>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <!-- 월 시간표 -->
        </div>
      </li>
      <!-- //월 날짜 -->

      <!-- 주 날짜 -->
      <li id="tab2" class="btnCon">
        <input type="radio" name="tabmenu" id="tabmenu2">
        <label for="tabmenu2">주</label>
        <div class="tabCon">
          <VueDatePicker 
            style="display: block;"
            v-model="week" 
            locale="kr" 
            :enable-time-picker="false" 
            :transitions="false" 
            :week-start="0"
            inline auto-apply 
            week-picker 
            :min-date="new Date()" 
            :position="rigth"
            />

          <!-- 주 시간표 -->
          <table>
            <thead>
              <tr>
                <th>시간</th>
                <th v-for="(day, index) in weekDays" :key="index">
                  {{ day.week }} ({{ day.day }})
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(hour, index) in hours" :key="index">
                <td>{{ hour }}</td>
                <td v-for="(day, dayIndex) in weekDays" :key="`${index}-${dayIndex}`">
                  <textarea v-model="day.texts[hour]"></textarea>
                </td>
              </tr>
            </tbody>
          </table>
          <!-- //주 시간표 -->
        </div>
      </li>
      <!-- //주 날짜 -->
    </ul>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue';
import moment from 'moment';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';

const date = ref(new Date());     // 월 날짜 데이터
const week = ref([new Date()]);   // 주 날짜 데이터
moment.locale('ko');              // 한국어로 변형

// 월 날짜 시간표
const monthitems = computed(() => {
  const items = [];
  const selectedDate = moment(date.value);

  for (let hour = 0; hour < 24; hour++) {
    const formattedDate = selectedDate.format('YYYY-MM-DD');  //현재 날짜와 시간
    const formattedTime = moment(hour, 'H').format('HH:mm');  //1 ~ 24

    //렌더링할 데이터
    items.push({
      time: formattedTime,
      text: `선택한 날짜는 ${formattedDate}입니다`
    });
  }
  return items;
});

// 시간 배열 생성
const hours = ref(Array.from({ length: 24 }, (_, i) => moment({ hour: i }).format('HH:mm')));

// 주 날짜 시간표
const weekDays = computed(() => {
  const startDate = moment(week.value[0]).startOf('week');

  return Array.from({ length: 7 }, (_, i) => {
    const date = startDate.clone().add(i, 'days');
    const dayName = date.format('ddd');
    const dayNumber = date.date();
    const texts = Object.fromEntries(hours.value.map(hour => [
      hour,
      `${date.format('YYYY-MM-DD')} ${dayName}`
    ]));

    return {
      date: date.format('YYYY-MM-DD'),
      week: dayNumber.toString().padStart(2, '0'),
      day: dayName, // 요일 이름
      texts: texts
    };
  });
});

</script>

