  max-width: 1000px;
  margin: 0 auto;
  position: relative;
}

.tabmenu ul {
  padding: 0;
  margin: 0;
  position: relative;
}

.tabmenu ul li {
  display: inline-block;
  width: 50%;
  float: left;
